<template>
  <div>
    <div>
      <van-nav-bar left-text="返回" left-arrow @click-left="onCancel">
        <template #right>
          <van-search placeholder="请输入搜索关键词" show-action v-model="value">
            <template #action>
              <div class="sousuo" @click.self="onSearch">搜索</div>
            </template>
          </van-search>
        </template>
      </van-nav-bar>
    </div>
    <div class="record clearfix">
      <div class="icon fr" v-show="text.length>0">
        <img src="../assets/icon/del.png" alt @click="empty" />
      </div>
      <ul class="clearfix list">
        <li
          class="fl"
          v-for="(item , index) in text[0]"
          :key="index"
          @click="goAbout(item)"
        >{{item}}</li>
      </ul>
    </div>
    <div class="foot">
      <h3>推荐菜谱</h3>
      <div>
        <ul class="clearfix">
          <li
            class="fl"
            v-for="(item , index) in list[0]"
            :key="index"
            @click="recommend(item)"
          >{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";

import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      value: "",
      text: [],
      list: []
    };
  },
  created() {
    //将搜索关键字渲染
    this.text.push(this.record);
    if (this.text[0] == "") {
      this.text.splice(0);
    }
    //请求推荐菜谱数据
    this.axios({
      method: "GET",
      url: "/byclass",
      params: {
        appkey: this.appkey,
        classid: 66,
        start: 0,
        num: 9
      }
    })
      .then(res => {
        this.list.push(res.data.result.list);
      })
      .catch(err => {
           
      });
  },
  computed: {
    ...mapState(["searchResult", "record"])
  },
  methods: {
    ...mapMutations(["searchData", "recordData", "emptyData", "changeData"]),
    // 点击搜索
    onSearch() {
      if (this.value == "") {
        return;
      }
      //请求搜索数据
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          keyword: this.value,
          num: 100,
          start: 0
        }
      })
        .then(res => {
          //将搜索结果加入vuex中
          this.searchData(res);
          this.$router.push({ name: "About" });
          //将搜索结果加入sessionStorage中
          sessionStorage.setItem("about", JSON.stringify(res));
        })
        .catch(err => {
             
        });
      //将关键字加入vuex中
      this.recordData(this.value);
      //将搜索关键字渲染
      this.text.push(this.record);

      this.value = "";
    },
    onCancel() {
      this.$router.back(-1);
    },
    //删除搜索记录
    empty() {
      this.text = [];
      //vuex搜索记录清除
      this.emptyData();
    },
    //点击搜索记录
    goAbout(item) {
     
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          keyword: item,
          num: 100,
          start: 0
        }
      })
        .then(res => {
          this.searchData(res);
          this.$router.push({ name: "About" });
        })
        .catch(err => {
             
        });
    },
    //点击推荐菜谱
    recommend(item) {
      this.$router.push({ name: "detail" });
      this.changeData({ key: item.name, value: item });
      sessionStorage.setItem("detail", JSON.stringify(item));
    }
  }
};
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar__right {
  right: 10px;
}
.sousuo {
  position: relative;
  left: 10px;
  color: #1989fa;
}

.record {
  .icon {
    width: 15px;
    height: 20px;
    margin-right: 30px;
    margin-top: -20px;
    img {
      width: 100%;
      height: 100%;
    }
  }

  .list {
    margin-top: 30px;
  }
  li {
    margin-left: 30px;
    margin-top: 20px;
    border: 1px solid #666;
    padding: 6px 3px;
    text-align: center;
    border-radius: 10px;
    color: #666;
    width: 80px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.foot {
  margin-left: 30px;
  h3 {
    margin-top: 60px;
    font-size: 20px;
  }
  ul {
    li {
      margin-left: 20px;
      margin-top: 15px;
      width: 80px;
      height: 20px;
      padding: 3px 5px;
      border: 1px solid #1989fa;
      border-radius: 5px;
      font-size: 12px;
      text-align: center;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #1989fa;
      white-space: nowrap;
      &:nth-child(3n + 1) {
        margin-left: 0;
      }
    }
  }
}
</style>